*{
  font-family: 'Fjalla One';
  margin: 0;
  padding: 0;
}

@mixin d-flex($justify:center,$align:center){
  display: flex;
  justify-content: $justify;
  align-items: $align;
}

body{
  background-color: #222;
}

.wrap{
  @include d-flex(space-between);
  margin: 0 100px;
}

@import"./map.scss";
@import"./list.scss";
